
import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Prospecto } from '../../../../modelos';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';

@Component({
	selector: 'app-prospecto',
	templateUrl: './prospecto.component.pug',
	styleUrls: ['./prospecto.component.styl']
})
export class ProspectoComponent implements OnInit {
	form: FormGroup;
	constructor(public dialogRef: MatDialogRef<ProspectoComponent>, @Inject(MAT_DIALOG_DATA) public data: Prospecto, private formBuilder: FormBuilder,) { }

	cerrar() {
		this.dialogRef.close();
	}

	ngOnInit() {
		this.form = this.formBuilder.group({
			nombre: [{value: this.data.nombre, disabled: true}],
			correo:[{value: this.data.correo, disabled: true}],
			empresa: [{value: this.data.empresa, disabled: true}],
			numero:[{value: this.data.numero, disabled: true}],
			asunto: [{value: this.data.asunto, disabled: true}],
		})		
	}

}