import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { UsuarioService } from '../../../../servicios';
import {MatSnackBar} from '@angular/material';

@Component({
	selector: 'app-agregar-usuario',
	templateUrl: './agregar-usuario.component.pug',
	styleUrls: ['./agregar-usuario.component.styl']
})
export class AgregarUsuarioComponent implements OnInit {

	form: FormGroup

	constructor(public dialogRef: MatDialogRef<AgregarUsuarioComponent>, private fomrBuilder: FormBuilder, public snackBar: MatSnackBar) { }

	ngOnInit() {
		this.form = this.fomrBuilder.group({
			correo: ['', [Validators.required, Validators.email]],
			contrasena: ['',Validators.required]
		})
	}

	submit() {
		if(this.form.valid){
			UsuarioService.crearUsuario({correo: this.form.controls.correo.value, password: this.form.controls.contrasena.value})
				.then(response => response && response.data ? this.dialogRef.close() : this.snackBar.open("no se creo el usuario", "", {duration: 1000}))
		}else{
			this.snackBar.open("Campos invalidos", "", {duration: 1000})
		}
		
	}

	cancel() {
		this.dialogRef.close(false);
	}
}
