import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';


/* Importamos los componentes que se usarán en las rutas
 */
import { AdminComponent } from './admin.component';
import { HomeComponent } from './home/home.component';
import { AuthGuard } from '../../guards/auth.guard';
import { LoginComponent } from './login/login.component';
import { UsuarioComponent } from './usuario/usuario.component';
import { UsuariosComponent } from './usuarios/usuarios.component';
import { ProspectosComponent } from './prospectos/prospectos.component';

const admin_routers: Routes = [
	{
		path: '',
		component: AdminComponent,
		// canActivate: [AuthGuard],
		// canActivateChild: [AuthGuard],
		children: [
			{
				path: '',
				component: HomeComponent
			},
			{
				path: 'perfil',
				component: UsuarioComponent
			},
			{
				path: 'prospectos',
				component: ProspectosComponent
			},
			{
				path: 'usuarios',
				component: UsuariosComponent
			},

		]
	},
	{
		path: 'login',
		component: LoginComponent
	},
];

@NgModule({
	imports: [
		RouterModule.forChild(admin_routers),
		CommonModule
	],
	exports: [
		RouterModule
	],
})
export class AdminRoutingModule { }
