import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource, MatDialog } from '@angular/material';
import { Usuario } from '../../../modelos';
import { UsuarioService } from '../../../servicios';
import { DetallesUsuarioComponent } from '../fragments/detalles-usuario/detalles-usuario.component';
import { AgregarUsuarioComponent } from '../fragments/agregar-usuario/agregar-usuario.component';
interface UsuarioInfoBasica {
	id: number,
	nombre: string,
	apellidos: string,
	correo: string,
	tipo: string
}


@Component({
	selector: 'app-usuarios',
	templateUrl: './usuarios.component.pug',
	styleUrls: ['./usuarios.component.styl']
})



export class UsuariosComponent implements OnInit {

	displayedColumns = [];
	dataSource: MatTableDataSource<UsuarioInfoBasica>;
	Usuarios: UsuarioInfoBasica[] = [];

	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;


	columnNames = [
		{
			id: "nombre",
			value: "Nombre"
		},
		{
			id: "correo",
			value: "Correo"
		},
		{
			id: "tipo",
			value: "Tipo"
		},		
	];

	constructor(private dialog: MatDialog) {
		this.displayedColumns = ['nombre','correo', 'tipo']
		this.dataSource = new MatTableDataSource(this.Usuarios);
		this.actualizarTabla();
	}

	ver(id) {
		const dialogRef = this.dialog.open(DetallesUsuarioComponent, {
			width: '60%',
			data: id
		});
	
		dialogRef.afterClosed().subscribe(result => {
			this.actualizarTabla()
		});
	}
 
	agregarUsuario(){
		const dialogRef = this.dialog.open(AgregarUsuarioComponent, {
			width: '60%',
		});
	
		dialogRef.afterClosed().subscribe(result => {
			this.actualizarTabla()
		});		
	}

	actualizarTabla(){
		UsuarioService.obtenerUsuarios()
		.then(r => r && r.data ? this.dataSource = new MatTableDataSource(r.data.map(u => {

			let usuario = new Usuario(u);
			let usuariobasico: UsuarioInfoBasica = {
				id: usuario.getId(),
				nombre: usuario.getNombre(),
				apellidos: usuario.getApellidos(),
				correo: usuario.getCorreo(),
				tipo: usuario.getTipo() ? usuario.getTipo() : 'estandar'
			}
			return usuariobasico;
		})) : null)	
	}

	applyFilter(filterValue: string) {
		this.dataSource.filter = filterValue.trim().toLowerCase();

		if (this.dataSource.paginator) {
		  this.dataSource.paginator.firstPage();
		}
	  }

	ngOnInit() {
		this.dataSource.paginator = this.paginator;
		this.dataSource.sort = this.sort;
	}

}
