
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource, MatDialog } from '@angular/material';
import { Prospecto } from '../../../modelos';
import { ProspectoService } from '../../../servicios';
import { ProspectoComponent } from './prospecto/prospecto.component';


@Component({
	selector: 'app-prospectos',
	templateUrl: './prospectos.component.pug',
	styleUrls: ['./prospectos.component.styl']
})
export class ProspectosComponent implements OnInit {
	displayedColumns = [];
	dataSource: MatTableDataSource<Prospecto>;
	prospectos: Prospecto[] = [];

	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;

	columnNames = [
		{
			id: "id",
			value: "id"
		},
		{
			id: "nombre",
			value: "Nombre"
		},
		{
			id: "correo",
			value: "Correo"
		},
		{
			id: "empresa",
			value: "Empresa"
		},
		{
			id: "numero",
			value: "Numero"
		},
	];
	constructor(private dialog: MatDialog) { 
		this.displayedColumns = this.columnNames.map(n => n.id);
		this.dataSource = new MatTableDataSource(this.prospectos);

		ProspectoService.obtenerProspectos()
			.then(prospectos => this.dataSource = new MatTableDataSource(prospectos))

	}

	applyFilter(filterValue: string) {
		this.dataSource.filter = filterValue.trim().toLowerCase();

		if (this.dataSource.paginator) {
		  this.dataSource.paginator.firstPage();
		}
	  }

	ver(row){
		console.log(row)
		this.dialog.open(ProspectoComponent, {
			width: '50%',
			disableClose: true,
			data: row
		})
	}


	ngOnInit() {
		this.dataSource.paginator = this.paginator;
		this.dataSource.sort = this.sort;		
	}

}