import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, FormGroup, Validators} from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { Usuario } from '../../../../modelos';
import { UsuarioService } from '../../../../servicios';
import {MatSnackBar, MatDialog} from '@angular/material';
import { ConfirmDelDialogComponent } from '../confirm-del-dialog/confirm-del-dialog.component';

@Component({
	selector: 'app-detalles-usuario',
	templateUrl: './detalles-usuario.component.pug',
	styleUrls: ['./detalles-usuario.component.styl']
})
export class DetallesUsuarioComponent implements OnInit {

	usuario: Usuario;
	form: FormGroup;
	loader = false;
	newContra = ''

	constructor(
		public dialogRef: MatDialogRef<DetallesUsuarioComponent>,
		@Inject(MAT_DIALOG_DATA) public data: number,
		private formBuilder: FormBuilder,
		public domSanitizer: DomSanitizer,public snackBar: MatSnackBar, private dialog: MatDialog) { }

	cancelar() {
		this.dialogRef.close()
	}

	guardar() {
		this.loader = true;
		console.log(this.usuario)
		if(this.form.valid) {
			UsuarioService.actualizarUsuario({
				id: this.usuario.getId(),
				nombre: this.usuario.getNombre(),
				apellidos: this.usuario.getApellidos(),
				correo: this.usuario.getCorreo(),
				tipo: this.usuario.getTipo(),
				password: this.newContra == '' ? null : this.newContra

			}).then(response => response && response.data ? (this.loader=false, this.snackBar.open("Cambios Guardados", "", {duration: 1000}), console.log(response.data)) : this.snackBar.open("No se guardo el usuario :(", "", {duration: 1000}))
		}else {
			this.snackBar.open("Algunos campos no son validos!", "", {duration: 1000});
			this.loader=false;
		}

		
	}

	eliminar(){
		const dialogRef = this.dialog.open(ConfirmDelDialogComponent, {
			width: '60%',
		});
	
		dialogRef.afterClosed().subscribe(result => {
			if(result){
				this.loader = true
				UsuarioService.eliminarUsuario(this.usuario.getId())
					.then(response => response && response.data ? (this.loader = false, this.dialogRef.close()) : null)
			}
		});	



	}

	ngOnInit() {
		this.usuario = new Usuario({});

		this.form = this.formBuilder.group({
			nombre: ['',Validators.required],
			correo: ['',Validators.required],
			tipo: ['',Validators.required],
			contra: [''],
		})

		UsuarioService.obtenerUsuario(this.data)
			.then(r => r && r.data? this.usuario = new Usuario(r.data) : null);
	}

}
